:root {
  --accent: #fd8dff;
  --body-font-size: 1rem;
  --body-line-height: 1.3rem;
  --header-height: 15rem;
  --row-height: 3rem;
  --row-line-height: 1.2rem;
  --row-padding: 2rem;
}

@media screen and (max-width: 650px) {
  :root {
    --body-font-size: 1rem;
    --body-line-height: 1.1rem;
    --header-height: 23rem;
    --row-height: 6rem;
    --row-line-height: 3rem;
    --row-padding: 1.5rem;
  }
}

a, a:visited {
  color: var(--accent-bg);
}

body {
  color: #000;
  padding: none;
  font-family: Inter, sans-serif;
  font-size: var(--body-font-size);
  background-color: #fff;
  grid-template-columns: max(20%, 9rem) auto;
}

body > header {
  background-color: var(--accent);
}

body > main {
  margin: 0;
  padding: 0;
}

.left-col {
  min-height: 100vh;
  background-color: #0f0f0f;
  grid-column: 1 / 1;
}

body > footer {
  color: #000;
  width: 100vw;
  background-color: var(--accent);
  border-top: none;
  position: fixed;
  bottom: 0;
}

.top {
  min-height: var(--header-height);
  max-height: var(--header-height);
}

.header {
  background-color: var(--accent);
  flex: 1;
  padding: 2rem;
  font-size: 2rem;
  line-height: 2.3rem;
}

.main-title {
  margin-bottom: 1.5rem;
}

.bodyfont {
  font-size: var(--body-font-size);
  line-height: var(--body-line-height);
}

.date {
  color: #fff;
  text-align: right;
  justify-content: flex-end;
  padding: 2rem;
  font-weight: bold;
}

.rows {
  margin: var(--row-padding) 0;
  flex-direction: column;
  display: flex;
}

.white {
  color: #fff;
  text-align: right;
  justify-content: right;
}

.rows > div {
  min-height: var(--row-height);
  max-height: var(--row-height);
  line-height: var(--body-line-height);
  padding: 0 var(--row-padding);
}

.rows > .b {
  font-weight: bold;
}

.rows > .key {
  background-color: var(--accent);
  color: #000;
}

.rows:last-child {
  margin-bottom: 0;
}

